@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Monserrat";
  font-display: swap;
  src: url("../fonts/Montserrat-Bold.woff2") format("woff2"), url("../fonts/Montserrat-Bold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 500;
  font-family: "Monserrat";
  font-display: swap;
  src: url("../fonts/Montserrat-Medium.woff2") format("woff2"), url("../fonts/Montserrat-Medium.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 600;
  font-family: "Monserrat";
  font-display: swap;
  src: url("../fonts/Montserrat-SemiBold.woff2") format("woff2"), url("../fonts/Montserrat-SemiBold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Monserrat";
  font-display: swap;
  src: url("../fonts/Montserrat-Regular.woff2") format("woff2"), url("../fonts/Montserrat-Regular.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Monserrat", "Arial", sans-serif;
  color: #011c40;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #f2f5f6;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: 100%;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #f2f5f6;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.container {
  width: 100%;
  max-width: 1366px;
  margin: 0 auto;
  padding-left: 103px;
  padding-right: 103px;
}
@media (max-width: 1023px) {
  .container {
    padding: 0 45px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 15px;
  }
}

.btn {
  display: block;
  padding: 10px 0;
  color: #011c40;
  background-color: #2c39f2;
  border: none;
  cursor: pointer;
  text-align: center;
  position: absolute;
  width: 100%;
  bottom: -64px;
}

.header {
  min-height: 650px;
  background-repeat: no-repeat;
  color: #f9fbfd;
  position: relative;
  background-image: url("../img/bg-image-desktop.webp");
}
@media (min-width: 1023px) and (min-resolution: 144dpi), (min-width: 1023px) and (min-resolution: 1.5dppx) {
  .header {
    background-size: cover;
    background-image: url("../img/bg-image-desktop@2x.webp");
  }
}
@media (max-width: 1023px) {
  .header {
    min-height: 638px;
    background: #2c39f2 url("../img/bg-image-tablet.webp");
    background-size: cover;
    background-repeat: no-repeat;
  }
}
@media (max-width: 1023px) and (min-resolution: 144dpi), (max-width: 1023px) and (min-resolution: 1.5dppx) {
  .header {
    background-image: url("../img/bg-image-tablet@2x.webp");
  }
}
@media (max-width: 767px) {
  .header {
    min-height: 500px;
    background: #2c39f2 url("../img/bg-image-mobile.webp");
    background-size: cover;
    background-repeat: no-repeat;
  }
}
@media (max-width: 767px) and (min-resolution: 144dpi), (max-width: 767px) and (min-resolution: 1.5dppx) {
  .header {
    background-image: url("../img/bg-image-mobile@2x.webp");
  }
}

.header__wrapper {
  display: flex;
  flex-wrap: nowrap;
  align-items: center;
  justify-content: space-between;
  padding-top: 33px;
}
@media (max-width: 1023px) {
  .header__wrapper {
    align-items: flex-start;
    padding-top: 28px;
  }
}
@media (max-width: 767px) {
  .header__wrapper {
    padding-top: 20px;
  }
}

@media (max-width: 767px) {
  .header__wrapper--open {
    display: flex;
    flex-direction: column;
    position: absolute;
    justify-content: start;
    right: 0;
    top: -20px;
    background-color: #f9fbfd;
    width: 89%;
    min-height: 100%;
    margin-right: 0;
    margin-top: 20px;
  }
  .header__wrapper--open + .header__description {
    display: none;
  }
  .header__wrapper--open .header__logo-image {
    fill: #011c40;
  }
}
.header__logo-image {
  fill: #f9fbfd;
}
@media (max-width: 1023px) {
  .header__logo-image {
    width: 38px;
    height: 20px;
    margin: 0;
  }
}
@media (max-width: 767px) {
  .header__logo-image {
    width: 42px;
    height: 22px;
  }
}
.header__logo-image:hover {
  opacity: 0.8;
}

.header__navigation--nojs {
  display: block;
}

.header__navigation-toogle--nojs {
  display: block;
}
.header__navigation-toogle--nojs::before {
  position: absolute;
  content: "";
  width: 24px;
  height: 14px;
  top: 24px;
  right: 15px;
  background-image: url("../img/svg/burger-menu.svg");
}

.header__navigation-toogle {
  display: none;
  cursor: pointer;
}
@media (max-width: 767px) {
  .header__navigation-toogle {
    display: block;
    border: none;
    background-color: transparent;
    width: 24px;
    height: 14px;
  }
}

.header__navigation-toogle--closed::before {
  position: absolute;
  content: "";
  width: 24px;
  height: 14px;
  top: 24px;
  right: 5px;
  background-image: url("../img/svg/closed.svg");
  background-repeat: no-repeat;
}

.header__navigation-toogle--opened::before {
  position: absolute;
  content: "";
  width: 24px;
  height: 14px;
  top: 24px;
  right: 15px;
  background-image: url("../img/svg/burger-menu.svg");
}

@media (max-width: 767px) {
  .header__navigation {
    display: none;
    margin-top: 36px;
    margin-left: 10px;
  }
}

@media (max-width: 767px) {
  .header__navigation--closed {
    display: block;
    color: #011c40;
    width: 100%;
    grid-column: 1/3;
  }
}

@media (max-width: 767px) {
  .header__navigation--closed .header__navigation-link {
    color: #011c40;
  }
}

.header__navigation-list {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
}
@media (max-width: 767px) {
  .header__navigation-list {
    display: flex;
    flex-direction: column;
  }
}

.header__navigation-item {
  padding-right: 33px;
}
.header__navigation-item:last-child {
  padding-right: 0;
}
@media (max-width: 1023px) {
  .header__navigation-item {
    padding-right: 24px;
